<template>
  <div>
    <div class="filterWrapper scrollable">
      <div>
        <div class="title">숙소타입</div>
        <div class="content type">
          <button class="all">전체</button>
          <button>호텔</button>
          <button>리조트</button>
          <button>펜션</button>
          <button>모텔</button>
          <button>게스트하우스</button>
          <button>레지던스</button>
          <button>카라반</button>
          <button>글램핑</button>
        </div>
      </div>
      <div>
        <div class="title">호텔성급</div>
        <div class="content star">
          <button>전체</button>
          <div>
            <div><button>1성급</button></div>
            <div><button>2성급</button></div>
            <div><button>3성급</button></div>
            <div><button>4성급</button></div>
            <div><button>5성급</button></div>
          </div>
        </div>
      </div>
      <div>
        <div class="title">총 숙박요금</div>
        <div class="content">
          <vue-slider @callback="priceSliderChange" ref="slider1" v-bind="price" v-model="price.value"></vue-slider>
        </div>
      </div>
      <div class="addition">
        <div class="title">편의시설</div>
      </div>
    </div>
    <button class="submit">필터적용</button>
  </div>
</template>

<script>
import vueSlider from 'vue-slider-component'
export default {
  components: {
    vueSlider
  },
  name: 'HotelFilter',
  data () {
    return {
      price: {
        value: [
          0,
          100
        ],
        width: '100%',
        height: 4,
        dotSize: 34,
        interval: 1,
        min: 0,
        max: 100,
        disabled: false,
        show: false,
        tooltip: 'none',
        bgStyle: {
          backgroundColor: '#b9b0ae'
        },
        processStyle: {
          backgroundImage: 'linear-gradient(to right, #c49358, #8b572a)',
          height: '4px'
        },
        sliderStyle: {
          boxShadow: '0 2px 4px 0 rgba(0, 0, 0, 0.09)',
          border: 'solid 1px #8b562b'
        }
      }
    }
  },
  methods: {
    priceSliderChange () {
      console.log('1?')
    }
  },
  mounted () {
    setTimeout(() => {
      this.price.show = true
    }, 160)
  }
}
</script>

<style scoped>
  .filterWrapper{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    bottom: 53px;
  }
  .filterWrapper > div{
    padding: 40px 20px 50px;
    border-bottom: 1px solid #ddd;
  }
  .filterWrapper > div:first-of-type{
    padding-bottom: 42px;
  }
  .filterWrapper > div:last-of-type{
    padding: 0;
    border-bottom: 0;
  }
  .filterWrapper div .title{
    font-size: 16px;
    color: #111;
    font-weight: 600;
    margin-bottom: 16px;
  }
  .filterWrapper > div:nth-of-type(2) .title {
    margin-bottom: 5px;
  }
  .filterWrapper > div .content.type{
    font-size: 0;
  }
  .filterWrapper > div .content.type > button{
    display: inline-block;
    margin-right: 8px;
    height: 36px;
    border-radius: 18px;
    border: 1px solid #b9b0ae;
    padding: 0 24px;
    background-color: #FFF;
    margin-bottom: 8px;
    font-size: 14px;
    color: #000;
  }
  .filterWrapper > div .content.type > button.all{
    background-color: #8b572a;
    color: #FFF;
    border: 0;
  }
  .filterWrapper > div .content.star > button{
    font-size: 14px;
    border: 0;
    background: transparent;
    font-weight: 300;
  }
  .filterWrapper > div .content.star > div{
    font-size: 0;
    margin-top: 16px;
  }
  .filterWrapper > div .content.star > div > div{
    display: inline-block;
    width: 20%;
    padding-right: 8px;
  }
  .filterWrapper > div .content.star > div > div:last-of-type{
    padding-right: 0;
  }
  .filterWrapper > div .content.star > div > div > button{
    width: 100%;
    height: 36px;
    border-radius: 18px;
    border: 1px solid #b9b0ae;
    padding: 0;
    background-color: #FFF;
    margin-bottom: 8px;
    font-size: 14px;
    color: #000;
  }
  .filterWrapper div.addition .title{
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
    margin-bottom: 0;
  }
  .submit{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    background-color: #262628;
    color: #FFF;
    height: 53px;
    border: 0;
  }
</style>
<style>
  .filterWrapper .vue-slider-dot:after{
    content: '';
    width: 4px;
    height: 4px;
    border-radius: 2px;
    background-color: #8b572a;
    display: block;
    margin-top: 14px;
    margin-left: 14px;
  }
</style>



