<template>
  <div class="hotelPop">
    <div v-bind:class="popping ? popping : ''">
      <div class="popHeader">
        <span>{{ popTitle($route.params.popFlag) }}</span>
        <button class="close" @click="$emit('close-pop')"></button>
      </div>
      <div class="popContent">
        <autocomplete
          v-if="$route.params.popFlag === 'destination'"
          :inputData="inputData"
        ></autocomplete>
        <Option
          v-if="$route.params.popFlag === 'option'"
          :inputData="inputData"
          @change-option="changeOption"
        ></Option>
        <Calendar
          v-if="$route.params.popFlag === 'date'"
          :dept="inputData.checkin"
          :retn="inputData.checkout"
          :isDept="true"
          @change-date="changeDate"
        ></Calendar>
        <hotel-filter
          v-if="$route.params.popFlag === 'filter'"
        ></hotel-filter>
      </div>
    </div>
  </div>
</template>

<script>
import Autocomplete from '@/components/Search/Autocomplete'
import Option from '@/components/Search/Option'
import Calendar from '@/components/Search/Calendar'
import HotelFilter from '@/components/HotelList/HotelFilter'

export default {
  components: {
    HotelFilter,
    Autocomplete,
    Option,
    Calendar
  },
  name: 'SearchPopup',
  props: ['inputData', 'popping'],
  methods: {
    changeDate (deptDate, retnDate) {
      this.inputData.checkin = deptDate
      this.inputData.checkout = retnDate
      this.$emit('close-pop')
    },
    changeOption (adults, children, room) {
      this.inputData.adults = adults
      this.inputData.children = children
      this.inputData.room = room
      this.$emit('close-pop')
    },
    popTitle (flag) {
      let title
      if (flag === 'destination') {
        title = ''
      } else if (flag === 'date') {
        title = '날짜선택'
      } else if (flag === 'option') {
        title = '숙박인원'
      } else if (flag === 'filter') {
        title = '상세조건'
      }
      return title
    }
  }
}
</script>
<style>
  #wmpHotel .scrollable{
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
  }
</style>
<style scoped>
  .hotelPop{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 20;
  }
  .hotelPop > div{
    width: 100%;
    height: 100%;
    background-color: #FFF;
    position: absolute;
  }
  .hotelPop > div.open{
    animation: openPopTop linear .15s;
  }
  .hotelPop > div.close{
    animation: closePopTop linear .15s;
  }
  .hotelPop > div .popHeader{
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #FFF;
    line-height: 60px;
    padding-left: 20px;
    border-bottom: 1px solid #ddd;
    color: #111;
    font-size: 16px;
  }
  .hotelPop > div .popHeader > button.close{
    position: absolute;
    background: transparent;
    right: 0;
    top: 0;
    height: 60px;
    width: 60px;
    border: 0;
    padding: 0;
    font-size: 32px;
    background-image: url(../../assets/close.png);
    background-repeat: no-repeat;
    background-position: center center;
  }
  .hotelPop > div .popContent {
    position: absolute;
    top: 60px;
    left: 0;
    bottom: 0;
    right: 0;
  }
  @keyframes openPopTop {
    from {
      margin-top: 100vh;
    }
    to {
      margin-top: 0;
    }
  }
  @keyframes closePopTop {
    from {
      margin-top: 0;
    }
    to {
      margin-top: 100vh;
    }
  }
</style>



