<template>
  <div class="inputContainer"
    v-bind:class="setClass"
    @click.stop
  >
    <button class="inputBtn destination"
      @click="$emit('open-pop', 'destination')"
      v-bind:class="!inputData.destination ? 'empty' : ''"
    >
      {{ inputData.destination || '지역명, 명소명 또는 호텔명 검색' }}
    </button>
    <div class="date">
      <button class="inputBtn" @click="$emit('open-pop', 'date')">
        <div>체크인<span></span>{{ getDayOfWeek(inputData.checkin) }}</div>
        <div class="roboto">{{ dateFormat(inputData.checkin) }}</div>
      </button>
      <button class="inputBtn" @click="$emit('open-pop', 'date')">
        <div>체크아웃<span></span>{{ getDayOfWeek(inputData.checkout) }}</div>
        <div class="roboto">{{ dateFormat(inputData.checkout) }}</div>
      </button>
    </div>
    <button class="inputBtn option" @click="$emit('open-pop', 'option')">
      <div>
        <div>숙박인원</div>
        <div>
          <span><span>성인</span> <span>{{ inputData.adults }}</span><span>명,</span>
          <span>어린이</span> <span v-if="inputData.children.length > 0">{{ inputData.children.length }}</span><span v-else>없음</span></span>
        </div>
      </div>
      <div>
        <div>객실 수</div>
        <div>{{ inputData.room }}개</div>
      </div>
    </button>
    <button class="submit"
      @click="submit()"
    ></button>
  </div>
</template>

<script>
export default {
  name: 'SearchContainer',
  props: ['inputData', 'subClass'],
  data () {
    return {
      dayOfWeek: ['일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일']
    }
  },
  computed: {
    setClass () {
      return this.subClass
    }
  },
  methods: {
    dateFormat (date) {
      const arrDate = date.split('-')
      return `${arrDate[0].substr(2, 2)}.${arrDate[1]}.${arrDate[2]}`
    },
    getDayOfWeek (date) {
      return this.dayOfWeek[(new Date(date)).getDay()]
    },
    submit () {
      const children = this.inputData.children.length > 0 ? this.inputData.children.join('&') : 'null'
      if (!this.inputData.destination) {
        alert('머무를 곳을 입력해주세요.')
      } else if (!this.inputData.checkin) {
        alert('체크인 날짜를 입력해주세요.')
      } else if (!this.inputData.checkout) {
        alert('체크아웃 날짜를 입력해주세요.')
      } else {
        this.$emit('close-search-pop')
        this.$cookies.set('bp_hotel', JSON.stringify(this.inputData), '15d')
        this.$router.push(
          `/hotelList/${this.inputData.destination}/${this.inputData.checkin}/${this.inputData.checkout}/${this.inputData.adults}/${children}/${this.inputData.room}`
        )
      }
    }
  }
}
</script>

<style scoped>
  .inputContainer.main{
    padding: 38px 20px 40px;
  }
  .inputContainer.sub{
    background: #5c6e7c;
    padding: 20px;
    position: absolute;
    width: 100%;
  }
  .inputContainer .inputBtn{
    border-radius: 4px;
    height: 80px;
    width: 100%;
    background-color: #FFF;
    text-align: left;
    margin-bottom: 8px;
    border: 0;
    padding: 0 20px;
    font-size: 14px;
    font-weight: 500;
  }
  .inputContainer .inputBtn.option{
    margin-bottom: 0;
  }
  .inputContainer .inputBtn.destination{
    font-size: 26px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .inputContainer .inputBtn.destination.empty{
    color: #bbb;
    font-size: 20px;
  }
  .inputContainer .date .inputBtn{
    float: left;
    width: 49%;
  }
  .inputContainer .date .inputBtn:first-child{
    margin-right: 2%;
  }
  .inputContainer .date .inputBtn > div:first-child{
    font-size: 12px;
  }
  .inputContainer .date .inputBtn > div:last-of-type{
    font-size: 22px;
  }
  .inputContainer .date .inputBtn > div > span{
    border-left: 1px solid #ddd;
    margin-left: 4px;
    margin-right: 4px;
    height: 12px;
    display: inline-block;
    top: 2px;
    position: relative;
  }
  .inputContainer .inputBtn.option > div{
    float: left;
  }
  .inputContainer .inputBtn.option > div:first-child {
    width: 70%;
    text-align: left;
  }
  .inputContainer .inputBtn.option > div:last-of-type{
    width: 30%;
    text-align: right;
  }
  .inputContainer .inputBtn.option > div > div:first-child{
    font-size: 12px;
  }
  .inputContainer .inputBtn.option > div > div:last-of-type{
    letter-spacing: -0.4px;
    font-size: 20px;
  }
  .inputContainer .submit{
    border-radius: 37px;
    height: 74px;
    margin: 27px auto 0;
    display: block;
    width: 174px;
    background: url(../../assets/search-warm.svg) no-repeat;
    background-size: cover;
    padding: 6px 0;
  }
</style>



