<template>
  <div>
    <div class="autoCompleteBox">
      <div class="searchIcon"></div>
      <input ref="cityInput" v-model="inputData.destination" placeholder="머무를 장소를 입력해주세요.">
    </div>
  </div>
</template>

<script>
export default {
  name: 'CitySearch',
  props: ['inputData'],
  mounted () {
    setTimeout(() => {
      this.$refs.cityInput.focus()
    }, 160)
  }
}
</script>

<style scoped>
  .autoCompleteBox{
    position: absolute;
    left: 16px;
    right: 60px;
    top: -60px;
    height: 60px;
  }
  .autoCompleteBox input{
    height: 40px;
    margin-top: 10px;
    background-color: #ffffff;
    border: solid 1px #d4d4d4;
    width: 100%;
    padding: 0;
    padding-left: 36px;
    font-size: 14px;
  }
  .searchIcon{
    width: 18px;
    height: 20px;
    position: absolute;
    pointer-events: none;
    background-image: url(../../assets/icon.png);
    background-repeat: no-repeat;
    background-size: contain;
    filter: brightness(0.1);
    top: 20px;
    left: 10px;
  }
</style>



