<template>
  <div>
    <div class="imgContainer"
      v-bind:style="{
        'background-image': getBg
      }"
    ><div></div></div>
    <div class="infoWrapper" ref="content">
      <div class="hotelInfo">
        <button @click="submit()">상세보기</button>
        <div>{{ hotelInfo.name }}</div>
        <div>{{ hotelInfo.address }}</div>
        <div>
          <span class="star" v-if="hotelInfo.star" v-for="x in hotelInfo.star" :key="x"></span>
          <span v-if="hotelInfo.rating">{{ hotelInfo.rating }}</span>
          <span v-if="hotelInfo.review" class="thin">({{ numberFormat(hotelInfo.review) }}개 리뷰)</span>
        </div>
        <button v-if="hotelInfo.image.length > 0">1/{{ hotelInfo.image.length }}</button>
      </div>
      <div class="agentList">
        <ul>
          <li><button>
            <div>₩381,400</div>
            <div>1박평균 ₩104,600</div>
            <img src="https://cdn6.agoda.net/images/MVC/default/agoda-logo.svg">
          </button></li>
          <li><button>
            <div>₩381,400</div>
            <div>1박평균 ₩104,600</div>
            <img src="https://t-ec.bstatic.com/static/img/tfl/group_logos/logo_booking/27c8d1832de6a3123b6ee45b59ae2f81b0d9d0d0.png">
          </button></li>
          <li><button>
            <div>₩381,400</div>
            <div>1박평균 ₩104,600</div>
            <img src="https://t-ec.bstatic.com/static/img/tfl/group_logos/logo_booking/27c8d1832de6a3123b6ee45b59ae2f81b0d9d0d0.png">
          </button></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['index', 'hotelInfo'],
  computed: {
    getBg () {
      return `url(${this.hotelInfo.image[0]})`
    }
  },
  methods: {
    submit () {
      this.$router.push(`/detail/${this.index}`)
    },
    numberFormat (number) {
      return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }
  }
}
</script>

<style scoped>
  .imgContainer{
    width: 100%;
    padding-bottom: 80%;
    background-size: cover;
    position: relative;
    background-position: center center;
  }
  .imgContainer > div:first-of-type{
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 250px;
    background-image: linear-gradient(to bottom, rgba(92, 110, 124, 0), #5c6e7c);
  }
  .infoWrapper{
    position:absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
  }
  .hotelInfo{
    padding: 126px 20px 0;
    position: relative;
  }
  .hotelInfo > *{
    color: #FFF;
  }
  .hotelInfo > button:first-of-type{
    width: 63px;
    height: 25px;
    background-color: rgba(216, 216, 216, 0.2);
    border: solid 1px rgba(255, 255, 255, 0.5);
    font-size: 12px;
    padding: 0;
  }
  .hotelInfo > div:first-of-type {
    margin-top: 12px;
    font-size: 20px;
    font-weight: 600;
  }
  .hotelInfo > div:nth-of-type(2){
    font-size: 12px;
    letter-spacing: -.3px;
    margin-top: 4px;
    line-height: 17px;
    font-weight: lighter;
  }
  .hotelInfo > div:nth-of-type(3){
    margin-top: 7px;
    font-size: 12px;
    letter-spacing: -.3px;
  }
  .hotelInfo > div:nth-of-type(2):before{
    width: 13px;
    height: 15px;
    float: left;
    content: '';
    margin-right: 5px;
    background: url(../../assets/location.svg) no-repeat;
  }
  .hotelInfo > button:last-of-type{
    position: absolute;
    right: 20px;
    bottom: 0;
    line-height: 22px;
    height: 22px;
    padding: 0;
    border: 0;
    background-color: transparent;
  }
  .hotelInfo > button:last-of-type:after{
    content: '＋';
    display: block;
    width: 22px;
    height: 22px;
    background-color: rgba(26, 26, 28, .8);
    font-size: 20px;
    float: right;
    margin-left: 4px;
  }
  .hotelInfo .star{
    margin-right: 6px;
    float: left;
    width: 13px;
    height: 13px;
    background: url(../../assets/star.svg) no-repeat;
  }
  .hotelInfo .star:last-of-type{
    margin-right: 0;
  }
  .hotelInfo .thin{
    font-weight: lighter;
  }
  .agentList{
    padding: 16px 20px 82px;
  }
  .agentList > ul > li > button{
    background-color: #EFF0F2;
    width: 100%;
    height: 80px;
    border-radius: 3px;
    margin-bottom: 10px;
    border: 0;
    padding: 0;
    position: relative;
    text-align: left;
    padding-left: 20px;
  }
  .agentList > ul > li:last-of-type > button{
    margin-bottom: 0;
  }
  .agentList > ul > li > button img{
    height: 20px;
    right: 18px;
    top: 30px;
    position: absolute;
  }
  .agentList > ul > li > button > div:first-of-type{
    font-size: 24px;
    letter-spacing: -.5px;
    color: #111;
  }
  .agentList > ul > li > button > div:nth-of-type(2){
    font-size: 12px;
    color: #999;
  }
</style>



