<template>
  <div class="listWrapper">
    <div class="searchPop"
      v-if="openSearchContainer"
      @click="openSearchContainer = false"
    >
      <search-container
        :inputData="inputData"
        :subClass="'sub'"
        @open-pop="openPop"
        @close-pop="closePop"
        @close-search-pop="openSearchContainer = false"
      ></search-container>
    </div>
    <div class="searchInfo">
      <div @click="openSearchContainer = !openSearchContainer">
        <div>{{ inputData.destination }}</div>
        <div>
          <span class="roboto">{{ dateFormat(inputData.checkin) }}</span>({{ getDayOfMonth(inputData.checkin) }})
          <span> - </span>
          <span class="roboto">{{ dateFormat(inputData.checkout) }}</span>({{ getDayOfMonth(inputData.checkout) }})
          <span> / {{ getBetweenDay(inputData.checkin, inputData.checkout) }}박</span>
          <span>{{ getTotalPassenger }}</span>
        </div>
        <button></button>
      </div>
      <div class="drop-shadow-down" v-bind:class="viewIndex === 0 ? '' : 'show'"></div>
    </div>
    <div class="hotelList"
      v-if="hotelList.length > 0"
      ref="hotelList"
      @touchstart="presentTouchStart"
      @touchend="presentTouchEnd"
      @touchmove="presentTouchMove"
    >
      <div class="hotelListCont before"
        v-if="viewIndex > 0"
        v-bind:style="getContStyle('before')"
        ref="before"
      >
        <hotel-info :index="viewIndex - 1" :hotelInfo = "hotelList[viewIndex - 1]"></hotel-info>
      </div>
      <div class="hotelListCont present"
        v-bind:style="getContStyle('present')"
        ref="present"
      >
        <hotel-info :index="viewIndex" :hotelInfo = "hotelList[viewIndex]"></hotel-info>
      </div>
      <div class="hotelListCont after"
        v-if="viewIndex < hotelList.length - 1"
        v-bind:style="getContStyle('after')"
        ref="after"
      >
        <hotel-info :index="viewIndex + 1" :hotelInfo = "hotelList[viewIndex + 1]"></hotel-info>
      </div>
    </div>
    <div class="drop-shadow-up" v-bind:class="viewIndex === hotelList.length - 1 ? '' : 'show'"></div>
    <div class="btnContainer">
      <button @click="openPop('filter')">상세조건</button>
      <button>정렬</button>
      <button>지도보기</button>
    </div>
    <search-popup
      v-show="$route.params.popFlag"
      :inputData="inputData"
      :popping="popping"
      @open-pop="openPop"
      @close-pop="closePop"
    ></search-popup>
  </div>
</template>

<script>
import SearchContainer from '@/components/Search/SearchContainer'
import SearchPopup from '@/components/Search/SearchPopup'
import HotelInfo from '@/components/HotelList/HotelInfo'

export default {
  components: {
    SearchContainer,
    SearchPopup,
    HotelInfo
  },
  data () {
    const inputData = {
      destination: this.$route.params.destination,
      checkin: this.$route.params.checkin,
      checkout: this.$route.params.checkout,
      adults: parseInt(this.$route.params.adults, 10),
      room: parseInt(this.$route.params.room, 10)
    }
    inputData.children = []
    if (this.$route.params.children !== 'null') {
      const arrChildren = this.$route.params.children.split('&')
      for (let x = 0; x < arrChildren.length; x += 1) {
        inputData.children.push(parseInt(arrChildren[x], 10))
      }
    }
    return {
      inputData,
      textOfMonth: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'],
      dayOfWeek: ['일', '월', '화', '수', '목', '금', '토'],
      openSearchContainer: false,
      popping: false,
      viewIndex: 0,
      hotelList: [],
      presentScale: 1,
      startOffsetY: 0,
      moveOffsetY: 0,
      listHeight: 0,
      scrollGap: 50,
      duringScroll: false
    }
  },
  computed: {
    getChildrenNumber () {
      return this.inputData.children.length
    },
    getTotalPassenger () {
      return parseInt(this.inputData.adults) + this.getChildrenNumber
    }
  },
  methods: {
    getContStyle (index) {
      const styleObj = {}
      const gap = this.startOffsetY - this.moveOffsetY
      if (index === 'before') {
        if (this.viewIndex === 0 || this.moveOffsetY === 0) {
          styleObj['opacity'] = 0
        } else if (this.startOffsetY < this.moveOffsetY) {
          const gapPercent = gap * -1 / this.listHeight
          styleObj['opacity'] = gapPercent
          const gapPercentBig = (gapPercent / 10) + 0.9
          styleObj['transform'] = `scale3d(${gapPercentBig},${gapPercentBig},${gapPercentBig})`
        } else {
          styleObj['opacity'] = 0
        }
      } else if (index === 'present') {
        if (this.moveOffsetY === 0) {
          styleObj['opacity'] = 1
        } else if (this.viewIndex !== this.hotelList.length - 1 && this.startOffsetY > this.moveOffsetY) {
          const gapPercent = 1 - (gap / this.listHeight)
          styleObj['opacity'] = gapPercent
          const gapPercentBig = (gapPercent / 10) + 0.9
          styleObj['transform'] = `scale3d(${gapPercentBig},${gapPercentBig},${gapPercentBig})`
        } else if (this.viewIndex !== 0 && this.startOffsetY < this.moveOffsetY) {
          styleObj['margin-top'] = `${(gap * -1)}px`
        } else {
          styleObj['opacity'] = 1
        }
      } else if (index === 'after') {
        if (this.moveOffsetY === 0) {
          styleObj['margin-top'] = `${this.listHeight}px`
        } else if (this.startOffsetY > this.moveOffsetY) {
          styleObj['margin-top'] = `${this.listHeight - gap}px`
        } else {
          styleObj['margin-top'] = `${this.listHeight}px`
        }
      }
      return styleObj
    },
    presentTouchStart (event) {
      if (this.duringScroll) {
        return
      }
      this.startOffsetY = event.changedTouches[0].pageY
    },
    presentTouchMove (event) {
      event.preventDefault()
      if (this.duringScroll) {
        return
      }
      this.moveOffsetY = event.changedTouches[0].pageY
    },
    presentTouchEnd (event) {
      if (this.moveOffsetY === 0 || this.duringScroll) {
        return
      }
      const gap = this.startOffsetY - this.moveOffsetY
      if (gap > this.scrollGap && this.viewIndex < this.hotelList.length - 1) {
        event.preventDefault()
        this.duringScroll = true
        this.scrollDown(this.$refs.after, 0, 200)
      } else if (gap < -150 && this.viewIndex > 0) {
        event.preventDefault()
        this.duringScroll = true
        this.scrollUp(this.$refs.present, this.listHeight, 200)
      } else {
        this.startOffsetY = 0
        this.moveOffsetY = 0
      }
    },
    getBetweenDay (sdate, edate) {
      const strStart = `${sdate.toString().substr(0, 4)}-${sdate.toString().substr(5, 2)}-${sdate.toString().substr(8, 2)}`
      const strEnd = `${edate.toString().substr(0, 4)}-${edate.toString().substr(5, 2)}-${edate.toString().substr(8, 2)}`
      function getDateObj (strDate) {
        const dateArray = strDate.split('-')
        return new Date(dateArray[0], Number(dateArray[1]) - 1, dateArray[2])
      }
      return (getDateObj(strEnd).getTime() - getDateObj(strStart).getTime()) / 86400000
    },
    dateFormat (date) {
      const newDate = new Date(date)
      return `${this.textOfMonth[newDate.getMonth()]}.${this.zeros(newDate.getDate())}`
    },
    getDayOfMonth (date) {
      const newDate = new Date(date)
      return this.dayOfWeek[newDate.getDay()]
    },
    zeros (n) {
      let zero = ''
      let newN = n
      newN = n.toString()
      if (newN.length < 2) {
        for (let i = 0; i < 2 - newN.length; i += 1) {
          zero += '0'
        }
      }
      return zero + newN
    },
    openPop (flag) {
      const objParam = this.$route.params
      objParam.popFlag = flag
      this.$router.push({
        params: objParam
      })
      this.popping = 'open'
      setTimeout(() => {
        this.popping = false
      }, 150)
    },
    closePop () {
      this.popping = 'close'
      setTimeout(() => {
        window.history.back()
      }, 150)
    },
    scrollDown (element, to, duration) {
      const start = parseInt(element.style.marginTop)
      const change = to - start
      const increment = 20
      let currentTime = 0
      const precentElement = this.$refs.present
      const animateScroll = () => {
        currentTime += increment
        var val = this.easeInOutQuad(currentTime, start, change, duration)
        element.style.marginTop = `${val}px`
        precentElement.style.opacity = val / this.listHeight
        const perBig = ((val / this.listHeight) * 0.1) + 0.9
        precentElement.style.transform = `scale3d(${perBig},${perBig},${perBig})`
        if (currentTime < duration) {
          setTimeout(animateScroll, increment)
        } else {
          this.startOffsetY = 0
          this.moveOffsetY = 0
          this.viewIndex += 1
          this.duringScroll = false
        }
      }
      animateScroll()
    },
    scrollUp (element, to, duration) {
      const start = parseInt(element.style.marginTop)
      const change = to - start
      const increment = 20
      let currentTime = 0
      const precentElement = this.$refs.before
      const animateScroll = () => {
        currentTime += increment
        var val = this.easeInOutQuad(currentTime, start, change, duration)
        element.style.marginTop = `${val}px`
        precentElement.style.opacity = val / this.listHeight
        const perBig = ((val / this.listHeight) * 0.1) + 0.9
        precentElement.style.transform = `scale3d(${perBig},${perBig},${perBig})`
        if (currentTime < duration) {
          setTimeout(animateScroll, increment)
        } else {
          this.startOffsetY = 0
          this.moveOffsetY = 0
          this.viewIndex -= 1
          this.duringScroll = false
        }
      }
      animateScroll()
    },
    easeInOutQuad (t, b, c, d) {
      t /= d / 2
      if (t < 1) return c / 2 * t * t + b
      t--
      return -c / 2 * (t * (t - 2) - 1) + b
    }
  },
  mounted () {
    setTimeout(() => {
      this.hotelList = [{
        name: '호텔1',
        address: '두바이, 국제금융센터',
        star: 4,
        rating: '9.0',
        review: 2546,
        image: [
          'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg'
        ]
      }, {
        name: '호텔2',
        address: '두바이, 국제금융센터',
        rating: '9.0',
        review: 2546,
        image: [
          'https://gotrip.s3.amazonaws.com/storage/go_trip/product/id/7/1/3/1/b/6/1/7131b61c225ecfb6a2ef65e57094449f/10002/9043f08df49faa258cb413d9e2154c5e.jpg'
        ]
      }, {
        name: '호텔3',
        address: '두바이, 국제금융센터',
        star: 5,
        rating: '9.0',
        review: 2546,
        image: [
          'http://www.thebling.co.kr/wp-content/uploads/2017/08/budapest-1500x1039.png'
        ]
      }, {
        name: '호텔4',
        address: '두바이, 국제금융센터',
        image: [
          'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg'
        ]
      }, {
        name: '호텔5',
        address: '두바이, 국제금융센터',
        star: 4,
        review: 2546,
        image: [
          'https://gotrip.s3.amazonaws.com/storage/go_trip/product/id/7/1/3/1/b/6/1/7131b61c225ecfb6a2ef65e57094449f/10002/9043f08df49faa258cb413d9e2154c5e.jpg'
        ]
      }, {
        name: '호텔6',
        address: '두바이, 국제금융센터',
        star: 3,
        rating: '8.0',
        review: 2546,
        image: [
          'http://www.thebling.co.kr/wp-content/uploads/2017/08/budapest-1500x1039.png'
        ]
      }, {
        name: '호텔7',
        address: '두바이, 국제금융센터',
        star: 3,
        rating: '9.0',
        review: 2546,
        image: [
          'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg'
        ]
      }]
    }, 1000)
    const vThis = this
    function findListHeight () {
      if (vThis.$refs.hotelList && vThis.$refs.hotelList.clientHeight > 0) {
        vThis.listHeight = vThis.$refs.hotelList.clientHeight
      } else {
        setTimeout(findListHeight, 100)
      }
    }
    if (this.$refs.hotelList && this.$refs.hotelList.clientHeight > 0) {
      this.listHeight = this.$refs.hotelList.clientHeight
    } else {
      setTimeout(findListHeight, 100)
    }
  }
}
</script>

<style scoped>
  .listWrapper{
    background-color: #5C6E7C;
    height: 100%;
  }
  .searchInfo{
    color: #FFF;
    background-color: #8F9D9D;
    width: 100%;
    z-index: 2;
    height: 70px;
    position: relative;
  }
  .searchInfo.fixed > div:first-of-type{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
  }
  .searchInfo > div:first-of-type{
    background-color: #8F9D9D;
    padding: 17px 0 0;
    height: 70px;
    text-align: center;
    width: 100%;
  }
  .searchInfo > div button{
    border-radius: 37px;
    padding: 0;
    height: 50px;
    width: 50px;
    border: 0;
    position: absolute;
    top: 10px;
    right: 18px;
    background: url(../../assets/search-small.png) no-repeat;
  }
  .searchInfo > div > div:first-of-type{
    line-height: 19px;
    height: 19px;
    font-size: 16px;
  }
  .searchInfo > div > div:last-of-type{
    line-height: 1.21;
    font-size: 14px;
    margin-top: 4px;
  }
  .hotelList{
    position: absolute;
    bottom: 0;
    left: 0;
    top: 70px;
    right: 0;
    overflow: hidden;
  }
  .hotelListCont{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #5c6e7c;
    height: 100%;
  }
  .searchPop{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 10;
  }
  .btnContainer{
    position: absolute;
    bottom: 16px;
    width: 200px;
    height: 46px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #1A1A1C;
    border-radius: 23px;
    font-size: 0;
    z-index: 2;
  }
  .btnContainer > button{
    color: #FFF;
    height: 46px;
    padding: 0;
    font-size: 12px;
  }
  .btnContainer > button:nth-child(2) {
    width: 28%;
    position: relative;
  }
  .btnContainer > button:nth-child(2):before{
    border-left: 1px solid #4d4d4d;
    content: '';
    position: absolute;
    left: 0;
    height: 16px;
    top: 15px;
  }
  .btnContainer > button:nth-child(2):after{
    border-right: 1px solid #4d4d4d;
    content: '';
    position: absolute;
    right: 0;
    height: 16px;
    top: 15px;
  }
  .btnContainer > button:first-of-type, .btnContainer > button:last-of-type{
    width: 36%;
  }
  .drop-shadow-down {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: -33px;
    height: 33px;
    opacity: 0;
    background-image: linear-gradient(to bottom, #1a1a1c, rgba(26, 26, 28, 0.48) 45%, rgba(26, 26, 28, 0) 98%);
    z-index: 1;
    transition: opacity .5s ease;
    pointer-events: none;
  }
  .drop-shadow-down.show{
    opacity: .25;
  }
  .drop-shadow-up {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 33px;
    opacity: 0;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
    z-index: 1;
    transition: opacity .5s ease;
    pointer-events: none;
  }
  .drop-shadow-up.show{
    opacity: .3;
  }
</style>



