<template>
  <div>
    <button class="reset" @click="reset">초기화</button>
    <div class="optionWrapper scrollable" ref="optionWrapper">
      <div>
        <div>객실</div>
        <div class="operateSpace">
          <button @click="roomNumChange(false)">－</button>
          <span>{{ room }}개</span>
          <button @click="roomNumChange(true)">＋</button>
        </div>
      </div>
      <div>
        <div>성인</div>
        <div class="operateSpace">
          <button @click="adultNumChange(false)">－</button>
          <span>{{ adults }}명</span>
          <button @click="adultNumChange(true)">＋</button>
        </div>
      </div>
      <div ref="childrenWrapper">
        <div>아동</div>
        <div class="operateSpace">
          <button @click="childrenNumChange(false)">-</button>
          <span>{{ children.length }}{{ children.length === 0 ? '' : '명' }}</span>
          <button @click="childrenNumChange(true)">+</button>
        </div>
      </div>
      <div class="childrenAge"
        v-for="(num, index) in children"
        v-bind:key="index"
      >
        <div
          v-bind:class="childrenFlag[index] ? 'selected' : ''"
          @click="toggleChildrenAge(index)"
        >아동 {{ index + 1 }}<span>{{ children[index] }}세</span><div class="clampIcon"></div></div>
        <div class="swiperSpace" v-show="childrenFlag[index]">
          <swiper
            :options="swiperOption"
            v-bind:ref="`swiper${index}`"
            @slideChange="changeAge(index)"
          >
            <swiper-slide v-for="age in 18" v-bind:key="age">{{ age - 1 }}세</swiper-slide>
          </swiper>
          <div class="backRoundCircle"></div>
        </div>
      </div>
    </div>
    <button class="submit"
      @click="$emit('change-option', adults, children, room)"
    >선택완료</button>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
export default {
  name: 'Option',
  components: {
    swiper,
    swiperSlide
  },
  props: ['inputData'],
  data () {
    const arrChildren = []
    const arrChildrenFlag = []
    for (let x = 0; x < this.inputData.children.length; x += 1) {
      arrChildren.push(this.inputData.children[x])
      arrChildrenFlag.push(false)
    }
    return {
      adults: this.inputData.adults,
      children: arrChildren,
      childrenFlag: arrChildrenFlag,
      room: this.inputData.room,
      swiperOption: {
        slidesPerView: 7,
        spaceBetween: 0,
        centeredSlides: true
      }
    }
  },
  methods: {
    reset () {
      this.adults = 1
      this.children = []
      this.childrenFlag = []
      this.room = 1
    },
    toggleChildrenAge (index) {
      this.$set(this.childrenFlag, index, !this.childrenFlag[index])
    },
    changeAge (index) {
      this.$set(this.children, index, this.$refs[`swiper${index}`][0].swiper.activeIndex)
    },
    roomNumChange (flag) {
      if (flag) {
        this.room += 1
      } else if (this.room > 1) {
        this.room -= 1
      }
    },
    adultNumChange (flag) {
      if (flag) {
        this.adults += 1
      } else if (this.adults > 1) {
        this.adults -= 1
      }
    },
    childrenNumChange (flag) {
      if (flag) {
        this.children.push(0)
        this.childrenFlag.push(false)
        this.scrollTo(this.$refs.optionWrapper, 200, 200)
      } else {
        this.children.pop()
        this.childrenFlag.pop()
      }
    },
    childrenAgeChange (index, flag) {
      let num = this.children[index]
      if (flag) {
        num += 1
      } else if (num > 0) {
        num -= 1
      }
      this.$set(this.children, index, num)
    },
    scrollTo (element, to, duration) {
      const start = element.scrollTop
      const change = to - start
      const increment = 20
      let currentTime = 0

      const animateScroll = () => {
        currentTime += increment
        var val = this.easeInOutQuad(currentTime, start, change, duration)
        element.scrollTop = val
        if (currentTime < duration) {
          setTimeout(animateScroll, increment)
        }
      }
      animateScroll()
    },
    easeInOutQuad (t, b, c, d) {
      t /= d / 2
      if (t < 1) return c / 2 * t * t + b
      t--
      return -c / 2 * (t * (t - 2) - 1) + b
    }
  },
  mounted () {
    for (let x = 0; x < this.inputData.children.length; x += 1) {
      this.$refs[`swiper${x}`][0].swiper.slideTo(this.inputData.children[x], 0)
    }
  }
}
</script>

<style scoped>
  button.reset{
    position: absolute;
    background: transparent;
    padding: 0 13px;
    height: 26px;
    border: 1px solid #bbb;
    border-radius: 13px;
    font-size: 14px;
    color: #999;
    right: 60px;
    top: -43px;
  }
  .optionWrapper{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 60px;
  }
  .optionWrapper > div:not(.childrenAge){
    padding: 30px 20px 29px;
    border-bottom: 1px solid #ddd;
    height: 100px;
    line-height: 40px;
  }
  .optionWrapper > div:not(.childrenAge) > div:first-child{
    font-weight: 600;
    font-size: 16px;
    color: #111;
    float: left;
  }
  .optionWrapper button{
    font-size: 24px;
    color: #8f9d9d
  }
  .operateSpace{
    width: 160px;
    text-align: center;
    position: relative;
    height: 40px;
    line-height: 38px;
    border: 1px solid #ddd;
    float: right;
    background-color: #f5f4f0;
  }
  .operateSpace > button{
    position: absolute;
    width: 40px;
    height: 38px;
    background-color: #fff;
    border: 0;
    padding: 0;
  }
  .operateSpace > button:first-child{
    left: 0;
    border-right: 1px solid #ddd;
  }
  .operateSpace > button:last-of-type{
    right: 0;
    border-left: 1px solid #ddd;
  }
  .optionWrapper > div.childrenAge{
    padding: 0;
    border: 0;
  }
  .optionWrapper > div.childrenAge > div:first-child{
    padding: 0 20px;
    box-shadow: inset 0 -1px 0 0 #ebebeb;
    margin: 0;
    height: 60px;
    line-height: 60px;
    position: relative;
    cursor: pointer;
    font-size: 14px;
    color: #111111;
  }
  .optionWrapper > div.childrenAge > div:first-child > span{
    float: right;
    margin-right: 28px;
    color: #8f9d9d;
    font-weight: 600;
    font-size: 16px;
  }
  .swiperSpace{
    width: 100%;
    text-align: center;
    position: relative;
    height: 100px;
    line-height: 100px;
    background-color: #f5f4f0;
  }
  .clampIcon{
    width: 24px;
    height: 24px;
    position: absolute;
    right: 12px;
    top: 18px;
    background-image: url('../../assets/chevron-top.png');
    transform: rotate(180deg);
    transition: .5s ease;
  }
  .selected .clampIcon{
    transform: rotate(0deg);
  }
  button.submit{
    position: absolute;
    bottom: 0;
    height: 60px;
    left: 0;
    right: 0;
    border: 0;
    color: #FFF;
    background-color: #262628;
    padding: 0;
    font-size: 16px;
    width: 100%;
  }
  .swiper-slide{
    font-size: 14px;
    position: relative;
    z-index: 2;
  }
  .swiper-slide.swiper-slide-active{
    color: #FFF;
  }
</style>
<style>
  .optionWrapper .swiper-container .swiper-notification{
    z-index: 0;
    opacity: 1;
    width: 58px;
    height: 58px;
    background-color: #8f9d9d;
    left: 50%;
    top: 50%;
    border-radius: 100%;
    margin-left: -29px;
    margin-top: -29px;
  }
</style>



