<template>
  <div id="wmpHotel"
    v-bind:style="{'min-height': offsetHeight + 'px', height: offsetHeight + 'px'}"
    ref="wmpHotel"
  >
    <div class="page">
      <transition name="fade">
        <router-view
          v-bind:key="getParamKey"
          :inputData="inputData"
          @change-input-data="changeInputData"
        />
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    let inputData = {}
    if (this.$cookies.get('bp_hotel')) {
      inputData = JSON.parse(this.$cookies.get('bp_hotel'))
    } else {
      const today = new Date()
      const tomorrow = new Date()
      tomorrow.setDate(today.getDate() + 1)
      inputData = {
        destination: '런던, 영국',
        checkin: `${today.getFullYear()}-${this.zeros(today.getMonth() + 1)}-${this.zeros(today.getDate())}`,
        checkout: `${tomorrow.getFullYear()}-${this.zeros(tomorrow.getMonth() + 1)}-${this.zeros(tomorrow.getDate())}`,
        adults: 1,
        children: [],
        room: 1
      }
    }
    return {
      inputData,
      offsetHeight: 0
    }
  },
  computed: {
    getParamKey () {
      const key = []
      Object.keys(this.$route.params).forEach((x) => {
        if (x !== 'popFlag') {
          key.push(this.$route.params[x])
        }
      })
      return key.join('/')
    }
  },
  methods: {
    changeInputData (newInput) {
      this.inputData = newInput
    },
    zeros (n) {
      let zero = ''
      let newN = n
      newN = n.toString()
      if (newN.length < 2) {
        for (let i = 0; i < 2 - newN.length; i += 1) {
          zero += '0'
        }
      }
      return zero + newN
    }
  },
  mounted () {
    const height = this.$refs.wmpHotel.offsetTop
    this.offsetHeight = window.innerHeight - height
  }
}
</script>

<style>
  @import url('https://fonts.googleapis.com/css?family=Roboto+Slab');
  @import url('https://fonts.googleapis.com/css?family=Nanum+Gothic');
  #wmpHotel{
    position: relative;
    color: #333;
    font-family: 'Apple SD Gothic Neo', 'Nanum Gothic', sans-serif;
    overflow: hidden;
  }
  #wmpHotel > div{
    height: 100%;
  }
  #wmpHotel .roboto{
    font-family: 'Roboto Slab', serif !important;
  }
  #wmpHotel * {
    box-sizing: border-box;
    touch-action: manipulation;
  }
  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  button {
    padding: 0;
    background: none;
    border: 0;
    cursor: pointer;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .2s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



