<template>
  <div class="bpWrapper">
    <div class="scrollable">
      <search-container
        :inputData="inputData"
        :subClass="'main'"
        @open-pop="openPop"
        @close-pop="closePop"
      ></search-container>
      <div class="recentSearch">
        <div>최근 검색한 여행</div>
        <ul>
          <li v-for="x in 3" v-bind:key="x">
            <div class="thumbnail"></div>
            <div>런던, 영국</div>
            <div>05.14(일) - 05.17(금) <span>| 성인 2명, 객실1개</span></div>
            <div class="close"></div>
          </li>
        </ul>
      </div>
    </div>
    <search-popup
      v-show="$route.params.popFlag"
      :inputData="inputData"
      :popping="popping"
      @close-pop="closePop"
    ></search-popup>
  </div>
</template>

<script>
import SearchContainer from '@/components/Search/SearchContainer'
import SearchPopup from '@/components/Search/SearchPopup'

export default {
  components: {
    SearchContainer,
    SearchPopup
  },
  props: ['inputData'],
  name: 'searchWrapper',
  data () {
    return {
      popping: false
    }
  },
  methods: {
    openPop (flag) {
      this.$router.push(flag)
      this.popping = 'open'
      setTimeout(() => {
        this.popping = false
      }, 150)
    },
    closePop () {
      if (!this.popping) {
        this.popping = 'close'
        setTimeout(() => {
          this.popping = false
          window.history.back()
        }, 150)
      }
    }
  }
}
</script>

<style scoped>
  .bpWrapper{
    height: 100%;
  }
  .bpWrapper .scrollable{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url('../../assets/backbg.jpg');
    background-size: cover;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
  .recentSearch{
    padding: 0 20px;
    padding-bottom: 30px;
  }
  .recentSearch > div{
    font-size: 20px;
    text-align: center;
    color: #FFF;
    letter-spacing: -.6px;
    margin-bottom: 24px;
  }
  .recentSearch ul{
    overflow: hidden;
    border-radius: 4px;
  }
  .recentSearch li{
    height: 72px;
    width: 100%;
    background-color: #FFF;
    cursor: pointer;
    border-bottom: 1px solid #ddd;
    padding: 16px;
    position: relative;
    padding-left: 70px;
  }
  .recentSearch li:last-of-type{
    border-bottom: 0;
  }
  .recentSearch li > div.thumbnail{
    width: 40px;
    height: 40px;
    background-color: #d5d5d5;
    border-radius: 100%;
    position: absolute;
    top: 16px;
    left: 16px;
  }
  .recentSearch li > div:nth-of-type(2){
    margin-top: 4px;
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
  }
  .recentSearch li > div:nth-of-type(3){
    font-size: 12px;
    font-weight: 500;
    color: #666;
    letter-spacing: -0.4px;
    margin-top: 4px;
    line-height: 15px;
  }
  .recentSearch li > div:nth-of-type(3) > span{
    color: #999;
  }
  .recentSearch li .close{
    width: 11px;
    height: 11px;
    background-image: url(../../assets/close-small.png);
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    cursor: pointer;
    right: 16px;
    top: 16px;
  }
</style>



